import React, { Component } from 'react'
import "./todo.css"

export default class todo extends Component {
state={content:"",
items:[]}

updateValue=(e)=>{this.setState({content:e.target.value})
 
}
store=(e)=>{
 this.setState({items:[...this.state.items,this.state.content],
content:""})

}

deleteItem=(index)=>{
   /* splice method is below and filter method is also there*/
    // const{items}=this.state
    // console.log(index);
    // items.splice(index,1)
    // this.setState({items:items})

    this.setState({items:this.state.items.filter((data,key)=>key!==index)})
   
}

editItems=(value,index)=>{
console.log(value);
return(
    <h1>sarath</h1>
    
)

}


    render() {
        const {content,items}=this.state
       console.log(items);
        return (
            <div>
              <div className="heading">
                  <h1>TodoApp</h1>
                  <input type="text" value={content} onChange={this.updateValue} placeholder="Enter Items" />
                 <button onClick={this.store}>Add Items</button>
                
            <div className="content">

               
           <ul>
               {items.map((value,index)=>{
                  return( <li key={index}>{value} <i onClick={()=>this.editItems(value,index)} class="fas fa-pen-fancy    "></i> <i  onClick={()=>this.deleteItem(index)} class="fa fa-trash" aria-hidden="true"></i> </li> )
               })}
                   
                 
                
                </ul>
            </div>
            </div> 
            </div>
        )
    }
}
