import React from 'react'
import "./header.css"
import{Link} from "react-router-dom"

export default function header() {
    return (
        <header>
          

            <h1 >Todo</h1>
           
            <div >
            <Link to="/">home</Link>
            <Link to="/about">about</Link>
            <Link to="/todo">todo</Link>
         
            </div>
            </header>
    )
}
